geojson_loader_handler = {

    properties: {
        type:{
            '1':{
                color: '#569835',
                text: 'Off-Street Path'
            },
            '2':{
                color: '#0069a6',
                text: 'On-Street Bike Lane'
            },
            '3':{
                color: '#944b93',
                text: 'Shared Roadway'
            },
            '4':{
                color: '#f7941d',
                text: 'Protected Bike Lane'
            }
        }
    },

    onLoad: function(){

    },


    loadData: function(data){
        var self = this;

        //Load Existing Data
        if (data == 'existing'){
            if (!mapData.geoJSON.existing){
                mapData.geoJSONLayers.existing = L.featureGroup();
                mapData.geoJSONLayers.existing.addTo(mapData.geoJSONLayer);

                mapData.geoJSON.existing = new L.GeoJSON.AJAX(["data/data.geojson"],{
                    clickable: true,
                    onEachFeature: self.geoJSONPopUpExisting,
                    style: {
                        color: '#000',
                        weight: 2,
                        opacity: 1,
                        className: "proposed",
                    }
                });
                mapData.geoJSON.existing.on('data:loading', function() {
                    map.spin(true);
                }.bind(this));
                mapData.geoJSON.existing.on('data:loaded', function() {
                  map.spin(false);
                }.bind(this));
            }
            else {
                mapData.geoJSONLayers.existing.addTo(mapData.geoJSONLayer);

            }
        }
        //Load Schools Data
        if (data == 'schools'){
            if (!mapData.geoJSON.schools){
                mapData.geoJSONLayers.schools = L.featureGroup();
                mapData.geoJSONLayers.schools.addTo(mapData.geoJSONLayer);

                mapData.geoJSON.schools = new L.GeoJSON.AJAX(["data/schools.geojson"],{
                    clickable: true,
                    onEachFeature: self.geoJSONPopUpSchools
                });
                mapData.geoJSON.schools.on('data:loading', function() {
                    map.spin(true);
                }.bind(this));
                mapData.geoJSON.schools.on('data:loaded', function() {
                  map.spin(false);
                }.bind(this));
            }
            else {
                mapData.geoJSONLayers.schools.addTo(mapData.geoJSONLayer);
            }
        }

        //Load City Halls Data
        if (data == 'cityhalls'){
            if (!mapData.geoJSON.cityhalls){
                mapData.geoJSONLayers.cityhalls = L.featureGroup();
                mapData.geoJSONLayers.cityhalls.addTo(mapData.geoJSONLayer);

                mapData.geoJSON.cityhalls = new L.GeoJSON.AJAX(["data/cityhalls.geojson"],{
                    clickable: true,
                    onEachFeature: self.geoJSONPopUpCityHalls
                });
                mapData.geoJSON.cityhalls.on('data:loading', function() {
                    map.spin(true);
                }.bind(this));
                mapData.geoJSON.cityhalls.on('data:loaded', function() {
                  map.spin(false);
                }.bind(this));
            }
            else {
                mapData.geoJSONLayers.cityhalls.addTo(mapData.geoJSONLayer);
            }
        }

        //Load Rail Stations Data
        if (data == 'railstations'){
            if (!mapData.geoJSON.railstations){
                mapData.geoJSONLayers.railstations = L.featureGroup();
                mapData.geoJSONLayers.railstations.addTo(mapData.geoJSONLayer);

                mapData.geoJSON.railstations = new L.GeoJSON.AJAX(["data/railstations.geojson?ver=2"],{
                    clickable: true,
                    onEachFeature: self.geoJSONPopUpRailStations
                });
                mapData.geoJSON.railstations.on('data:loading', function() {
                    map.spin(true);
                }.bind(this));
                mapData.geoJSON.railstations.on('data:loaded', function() {
                  map.spin(false);
                }.bind(this));
            }
            else {
                mapData.geoJSONLayers.railstations.addTo(mapData.geoJSONLayer);
            }
        }



    },

    generatePopUpExisting: function(id){
        var properties = mapData.geoJSONData.existing[id].feature.properties;
        var popup = "<div><p>" +
            "<b>Name:</b> <span class='cap'> " + (properties.Name || 'N/A') +"</span><br>" +
            "<b>Class:</b> <span class='cap'> " + (geojson_loader_handler.properties.type[properties.Class].text || 'N/A') +"</span><br>" +
            "</p></div>";

        return popup;
    },

    generatePopUpSchools: function(id){
        var properties = mapData.geoJSONData.schools[id].feature.properties;
        var popup = "<div><p>" +
            "<b>Name:</b> <span class='cap'> " + (properties.NAME || 'N/A') +"</span><br>" +
            "<b>Address:</b> <span class='cap'> " + (properties.ADDRESS || 'N/A') +"</span><br>" +
            "</p></div>";

        return popup;
    },

    generatePopUpCityHalls: function(id){
        var properties = mapData.geoJSONData.cityhalls[id].feature.properties;
        var popup = "<div><p>" +
            "<b>Name:</b> <span class='cap'> " + (properties.PARENT_AGENCY || 'N/A') +"</span><br>" +
            "<b>Address:</b> <span class='cap'> " + (properties.ADDRESS || 'N/A') +"</span><br>" +
            "</p></div>";

        return popup;
    },

    generatePopUpRailStations: function(id){
        var properties = mapData.geoJSONData.railstations[id].feature.properties;
        var popup = "<div><p>" +
            "<b>Station Name:</b> <span class='cap'> " + (properties.STATION || 'N/A') +"</span><br>" +
            "<b>Agency:</b> <span class='cap'> " + (properties.USER_ || 'N/A') +"</span><br>" +
            "</p></div>";

        return popup;
    },

    geoJSONPopUpExisting: function(f,l){
        var self = this;
        var id = f.properties.OBJECTID;
        var type = f.properties.Class;
        mapData.geoJSONData.existing[id] = l;
        l.setStyle({
            color: geojson_loader_handler.properties.type[type].color || '#000',
            id: id,
            features: f.properties,
        });

        mapData.geoJSONData.existing[id].bindPopup(geojson_loader_handler.generatePopUpExisting(id));
        mapData.geoJSONData.existing[id].addTo(mapData.geoJSONLayers.existing);

    },

    geoJSONPopUpSchools: function(f,l){
        var self = this;
        var id = f.properties.OBJECTID;
        mapData.geoJSONData.schools[id] = l;
        var existingIcon = L.icon({
            iconUrl: 'img/schools.png',
            iconSize: [16, 16],
            iconAnchor: [8, 8],
            popupAnchor: [0, -10],
            className: "points-icons"
        });
        l.setIcon(existingIcon);

        mapData.geoJSONData.schools[id].bindPopup(geojson_loader_handler.generatePopUpSchools(id));
        mapData.geoJSONData.schools[id].addTo(mapData.geoJSONLayers.schools);

    },

    geoJSONPopUpCityHalls: function(f,l){
        var self = this;
        var id = f.properties.ID;
        mapData.geoJSONData.cityhalls[id] = l;
        var existingIcon = L.icon({
            iconUrl: 'img/cityhalls.png',
            iconSize: [16, 16],
            iconAnchor: [8, 8],
            popupAnchor: [0, -10],
            className: "points-icons"
        });
        l.setIcon(existingIcon);

        mapData.geoJSONData.cityhalls[id].bindPopup(geojson_loader_handler.generatePopUpCityHalls(id));
        mapData.geoJSONData.cityhalls[id].addTo(mapData.geoJSONLayers.cityhalls);

    },

    geoJSONPopUpRailStations: function(f,l){
        var self = this;
        var id = f.properties.ID;
        var agency = f.properties.USER_;
        mapData.geoJSONData.railstations[id] = l;
        var existingIcon = L.icon({
            iconUrl: 'img/railstations-' + agency + '.png',
            iconSize: [16, 16],
            iconAnchor: [8, 8],
            popupAnchor: [0, -10],
            className: "points-icons"
        });
        l.setIcon(existingIcon);

        mapData.geoJSONData.railstations[id].bindPopup(geojson_loader_handler.generatePopUpRailStations(id));
        mapData.geoJSONData.railstations[id].addTo(mapData.geoJSONLayers.railstations);

    },

    events: function() {
        var self = this;
        self.onLoad();
    }
}